@import url('https://fonts.googleapis.com/css?family=Open+Sans|Playfair+Display+SC');

.App {
  padding: 1em;
}

h1{
  font-family: 'Playfair Display Sc';
  text-align: center;
  font-size: 64px;
}

label {
  display: inline-block;
  width: 100px;
}

header {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

/* typography */

html {
  font-family: 'helvetica neue', sans-serif;
}

thead th, tfoot th {
  font-family: 'Rock Salt';
}

th {
  letter-spacing: 2px;
  padding: 10px;
}

td {
  letter-spacing: 1px;
  overflow-x:hidden;
  overflow-y:hidden;
  font-size: 12px;
  padding: 10px;
  text-align: center;
}

/* table */

table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  overflow:hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}


.content_td p,
.content_td td {
  max-width: 100%;
  max-height: 100px;
  overflow-y: scroll;
  text-overflow: ellipsis;
}



img {
 object-fit: scale-down;
 width: 100%;
 height: 100%;
}

td,
th{
  width: 10%;
}

th{
  font-weight: 700;
}


td:nth-child(4),
td:nth-child(5),
th:nth-child(4),
th:nth-child(5){
  width: 30%;
 
 
}


tr:nth-child(odd),
tbody:nth-child(odd){
  background-color: #fff0c7;
}
